
@import '../css/github-light.css';
pre {
    // border: 2px dashed #aaa;
    padding: .2rem;
    margin: .2rem 0;
    background: #f0f5f7;
    overflow-x: auto;
    border-radius: 3px
}
code {
    background: #f0f5f7;
    padding: .02rem .08rem;
    border-radius: 3px
}
.article article {
    text-align: left;
}
.article h1 {
    text-align: center;
    margin-bottom: .2rem;
    padding: .2rem;
}
.article a.anchor {
    display: none;
}
.article a {
    color: #1d71c0;
    text-decoration: underline;
}
.article article h2,
.article article h3 {
    margin: .4rem 0 .2rem -.4rem;
    padding-left: .4rem;
    border-left: 3px solid #1d71c0;
}
.article article h3 {
    margin-left: 0;
    padding-left: 0;
    border-left: none;
}
.article article h4 {
    margin: .2rem 0;
}
ol {
    margin: .2rem;
}
ul {
    margin: .2rem;
    list-style: none;
}
ul li::before {
    content: '';
    color: #1d71c0;
    margin-right: .1rem;
}
.comment {
    width: 70%;
    margin: 0 auto;
    background: #fff;
    padding: .2rem .3rem;
    border-radius: 3px;
    box-shadow: 1px 1px rgb(199, 206, 200),2px 2px rgb(199, 206, 200),3px 3px rgb(199, 206, 200);
}



